import { Component, ViewChild } from '@angular/core';
import { Nav, MenuController, Platform, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { IonicStorageProvider } from '../providers/base-settings/storage';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  private rootPage: string;
  private pages: Array<{icon: string, title: string, component: any}>;
  private sessionuser: any;

  /**
   * Constructor
   */
  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen,
    public menuCtrl: MenuController, private ionStorage: IonicStorageProvider, private event: Events) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { icon: 'home', title: 'Dashboard', component: 'DashboardPage' },
      { icon: 'stats', title: 'Elections', component: 'ElectionsPage' },
      { icon: 'disc', title: 'Issues', component: 'IssuesPage' }
    ];

    // Determine Root Page after checking user session
    this.rootPage = 'LoginPage';

    /**
     * Subscribe to the logout event
     */
    this.event.subscribe('login', (data) => {
      this.setLoginCredentials(data);
      this.event.unsubscribe('login');
    });

    /**
     * Subscribe to the logout event
     */
    this.event.subscribe('logout', () => {
      this.logout();
      this.event.unsubscribe('logout');
    });
  }

  /**
   * Initialize App to set splash and status bar
   */
  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  /**
   * Open Respective Page
   */
  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }

  /**
   * Update user credentials after login
   */
  setLoginCredentials(sessionuser) {
    let env = this;
    env.sessionuser = sessionuser;
  }

  /**
   * Logout
   */
  logout() {
    this.ionStorage.removeItem('sessionuser');
    this.menuCtrl.close();
    this.openPage({ component: 'LoginPage' });
  }
}



