import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { Storage } from '@ionic/storage';

@Injectable()
export class IonicStorageProvider {
  /**
   * Initialize
   */
	constructor(public storage: Storage) {

	}

  /**
   * Get Storage Items
   */
  getItems(arrayname: string) {
    return this.storage.get(arrayname).then(value => {
			return value;
		});
  }

  /**
   * Get Storage Item Details
   */
  getItemDetails(arrayname: string, itemId: string) {

  }

	/**
   * Set Storage Item
   */
  setItem(arrayname: string, itemtostore: object) {
    return this.storage.set(arrayname, itemtostore).then(value => {
			return value;
		});
  }

  /**
   * Update Storage Item
   */
  updateItem(arrayname: string, itemtoupdate: object) {
		return this.storage.set(arrayname, itemtoupdate).then(value => {
			return value;
		});
  }

	/**
   * Remove Storage Item
   */
  removeItem(arrayname: string) {
    this.storage.remove(arrayname);
  }
}



