import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';

import { Storage } from '@ionic/storage';

@Injectable()
export class BaseSettingsProvider {
	env = {
		"dev": "development",
		"stg": "staging",
		"pro": "production"
	};
	url: string;

	/**
   * Initialize
   */
	constructor(public storage: Storage) {
		// Set the env to either dev or prod;
		const ENV = this.env.dev;

		// Switch case to determine the environment to use
		switch (ENV) {
			case "development":
				this.url = "http://dev.apis.simplerealty.in/";
			break;

			case "staging":
				this.url = "http://stg.apis.simplerealty.in/";
			break;

			case "production":
				this.url = "http://apis.simplerealty.in/";
			break;

			default:
				this.url = "http://apis.simplerealty.in/";
			break;
		}
	}
}



